import { Inject, Injectable, Optional, OpaqueToken } from '@angular/core';
import { MessageService } from './message.service';

/**
 * Localization prefix for the component messages.
 *
 * For internal use.
 * @hidden
 */
export const L10N_PREFIX = new OpaqueToken('Localization key prefix');

/**
 * Component localization service.
 *
 * For internal use.
 * @hidden
 */
@Injectable()
export class LocalizationService {
  private dictionary: {[key: string]: string} = {};

  constructor(@Inject(L10N_PREFIX) private prefix: string,
              @Optional() private messageService: MessageService) {}

  public get(shortKey: string): string {
    const key = this.key(shortKey);

    let message;
    if (this.messageService) {
      message = this.messageService.get(key);
    }

    return message || this.dictionary[key];
  }

  public register(shortKey: string, value: string, override: boolean = false): void {
    const key = this.key(shortKey);
    if (!override && this.dictionary[key]) {
      return;
    }

    this.dictionary[key] = value;
  }

  private key(shortKey: string): string {
    return this.prefix + '.' + shortKey;
  }
}



